.container a:link, .container a:visited, .container a:hover, .container a:active {
	color: inherit;
	font-weight: bold;
	text-emphasis: none;
    text-decoration: none;
}

.subtitle {
	font-weight: lighter;
}

div:global(.main) section div.buttonSection {
	column-gap: 50px;
	row-gap: 20px;
	width: 100%;
	display: grid;
	grid-template-columns: repeat(3, 30%);
	justify-content: center;
	align-items: center;
}
@media only screen and (max-width: 600px) {
	div:global(.main) section div.buttonSection {
		grid-template-columns: unset;;
		grid-template-rows: repeat(3, 1fr);
		width: unset;
	}
}

div:global(.main) section div.priceButton:global(.download) {
	display: grid;
	grid-auto-rows: 35px;
	row-gap: 30px;
	font-size: large;
	padding: 20px;
	box-sizing: border-box;
	background-color: rgba(255, 255, 255, 0.95);
	height: 100%;
	width: 100%;
	max-width: unset;
	margin: 0px;
}

div.priceButton > * {
	display: flex;
	align-items: center;
	justify-content: center;
	white-space: pre;
	word-break: break-word;
	overflow-wrap: break-word;
	width: 100%;
}

div.priceButton > a {
	background-color: gold;
	width: 100%;
}

.description {
	font-weight: 300;
	line-height: 1.5em;
	background-color: rgba(128, 250, 203, 0.8);
	padding: 20px;
	box-sizing: border-box;
	border-radius: 5px;
	text-align: left;
}

.notice {
	font-style: italic;
}