/* Depth: 0 */

#root {
	display: flex;
	font-family: Roboto,"Source Sans Pro","Open Sans","San Francisco",Helvetica,Arial,sans-serif;
	text-align: center;
    justify-content: center;
	font-size: larger;
	color: rgba(10, 10, 20, 1)
}

/* Depth: 1 */

.background-gradient {
	background-color: white;
	background-image: linear-gradient(
		160deg,
		rgba(4, 36, 116, 0.6),
		rgba(70, 83, 109, 0.8),
		rgba(164, 52, 62, 0.78),
		rgba(152, 81, 86, 0.85)
	);
	z-index: -2;
	-webkit-filter: brightness(1.8);
	        filter: brightness(1.8);
	position: fixed;
	height: 100%;
	width: 100%;
}

.background {
	background-image: url('/images/background.png');
	opacity: 0.5;
	background-repeat: no-repeat;
	background-size: cover;
	background-position: 50%;
	background-color: white;
	position: fixed;
	height: 100%;
	width: 100%;
}

.main {
	display: flex;
	flex-direction: column;
	font-family: 'GT-Walsheim';
	padding-top: 12vh;
	z-index: 1;
	width: 100%;
}
.main * {
	scroll-margin-top: 8vh;
}

/* Depth: 2 */

.main > section, .main > div {
    flex: 0 1 auto;
	flex-shrink: 0;
	display: flex;
	flex-direction: column;
	align-items: center;
	padding: 50px 0px 30px 0px;
}

.main > section a:link, .main > section  a:visited, .main > section a:hover, .main > section a:active {
	color: inherit;
	font-weight: bold;
	-webkit-text-emphasis: none;
	        text-emphasis: none;
	text-decoration: none;
	cursor: pointer;
}

@media only screen and (max-width: 700px) {
	.App {
		display: flex;
		flex-direction: column;
	}
	@supports not ((display: flex) or (display: grid)) {
		.main {
			display: flex;
		}
	}
}

/* Depth: 3 */

.wrapper {
	width: 1040px;
	max-width: 95%;
	display: flex;
	flex-direction: column;
	align-items: center;
}
.wrapper > :not(div):not(a):not(img) {
	align-self: flex-start;
	text-align: left;
}

/* Depth: 4 */

.columns {
	display: grid;
	width: 100%;
	grid-template-columns: 1fr 1fr;
	grid-column-gap: 30px;
	-webkit-column-gap: 30px;
	        column-gap: 30px;
	grid-row-gap: 30px;
	row-gap: 30px;
}
@media only screen and (max-width: 600px) {
	.columns {
		grid-template-columns: unset;
		grid-auto-rows: auto;
	}

}
.column-text {
	display: flex;
	flex-direction: column;
	text-align: left;
	box-sizing: border-box;
	align-items: stretch;
}

/* Additional */

.column-text > *:first-child {
	-webkit-margin-before: 0px;
	        margin-block-start: 0px;
}
p:first-of-type {
    -webkit-margin-before: 0px;
            margin-block-start: 0px;
}
ol, ul {
	-webkit-margin-before: 0.5em;
	        margin-block-start: 0.5em;
	-webkit-margin-after: 0px;
	        margin-block-end: 0px;
}

.normal {
	font-weight: normal;
}

.highlighted {
	color: rgb(233, 198, 0);
}
@supports (-webkit-text-stroke: 1px black) {
	.highlighted {
	  -webkit-text-stroke: 0.5px black;
	}
}

.bold {
	font-weight: bold;
}

.bolder {
	font-weight: bolder;
}

.underlined {
	text-decoration: underline;
}

.strikethrough {
	text-decoration: line-through;
}

.main label, .light {
	font-weight: 300;
}

/* Buttons */

.buttonSection {
	display: grid;
	grid-auto-columns: 1fr;
